import { mapState, mapActions } from "vuex";
import { Toast } from "mint-ui";
import vHeader from "@/components/v-header";
const qs = require("qs");
export default {
  data() {
    return {
      promoterUserId: '',
      phone: "",
      pwd: "",
      code: '',
      time: 60,
      hasSend: false,
      typeSet: 'password',
      statusShow: true
    };
  },
  components: {
   vHeader
  },
  computed: {
    ...mapState(["token", "userId", "parentId"])
  },
  beforeCreate() {
    document.title = "注册";
  },
  created() {
    document.body.classList.add('bg-white');
  },
  destroyed(){
    document.body.classList.remove('bg-white');
  },
  methods: {
    ...mapActions(["atnUserId", "atnWeChatInfo"]),
    //改变密码显示
    changeStatus(){
      if(this.statusShow){
        this.statusShow = false;
        this.typeSet = 'text';
      }else{
        this.statusShow = true;
        this.typeSet = 'password';
      }
    },
    // 获取验证码
    getCode() {
      if (this.phone.length !== 11) {
        this.showTip("手机号码格式不正确");
        return;
      }
      this.$axios
        .get(this.api.getRegCode+this.phone)
        .then(res => {
          const resData = res.data;
          if (resData.code != 1) {
            this.showTip(resData.msg);
            return;
          }
          // 成功之后改变颜色，60秒后可再次发送
          this.hasSend = true;
          this.showTip("验证码已发送到您的手机，请注意查收");
          this.countDownTime();
        })
        .catch(res => {
          this.showTip("获取验证码失败，请稍后重试");
        });
    },
    // 验证码倒计时
    countDownTime() {
      let fnCount = () => {
        let count = setTimeout(() => {
          if (this.time <= 0) {
            clearTimeout(count);
            this.hasSend = false;
            this.time = 60;
          } else {
            this.time -= 1;
            fnCount();
          }
        }, 1000);
      };
      fnCount();
    },
    // 注册绑定手机号
    reg() {
      if (this.phone.length !== 11) {
        this.showTip("手机号码格式不正确");
        return;
      }
      if (this.code === "") {
        this.showTip("请输入验证码");
        return;
      }
      const ajaxData = {
        userName: this.phone,
        password: this.pwd,
        smsCode: this.code
      };
      this.promoterUserId?ajaxData.promoterUserId=this.promoterUserId:'';
      this.$axios
        .post(this.api.register, JSON.stringify(ajaxData), {
          headers: {"content-type": "application/json"}
        })
        .then(res => {
          const resData = res.data;
          if (resData.code !== 1) {
            this.showTip(resData.msg);
            return;
          }else{
            this.showTip('注册成功');
            let _this = this;
            setTimeout(function(){ 
              _this.$router.push('/mine/login'); 
            }, 1000);
          }
          console.log(resData);
        })
        .catch(res => {
          loading.close();
          this.showTip("注册失败，请稍后重试");
      });
    },
    // 存储userId和微信信息后返回
    setUserIdBack(objData) {
      this.atnUserId(objData.encryptionId);
      this.atnWeChatInfo({
        name: objData.nickName,
        avatar: objData.wechatHeadImageUrl
      });
      const bindOk = Toast({
        msg: "绑定成功！",
        iconClass: "ok",
        duration: 2000
      });
      setTimeout(() => {
        this.$router.go(-1);
      }, 2600);
    }
  }
};



